<script>
import {exportTableData} from '../../assets/js/tableExport'
export default {
  name: "examAnalysis",
  data() {
    return {
      dateRange: "", //时间选择
      sectionName: "", //断面名称
      value: "",
      stationId:'',
      optionData: [
        { value: 1, label: "水温", standard: "", isShow: true },
        { value: 2, label: "PH", standard: "6~9", isShow: true },
        { value: 3, label: "溶解氧", standard: "≥5", isShow: true },
        { value: 4, label: "电导率", standard: "", isShow: true },
        { value: 5, label: "浊度", standard: "", isShow: true },
        { value: 6, label: "高锰酸盐", standard: "6", isShow: true },
        { value: 7, label: "氨氮", standard: "1", isShow: true },
        { value: 8, label: "总磷", standard: "0.2", isShow: true },
        { value: 9, label: "总氮", standard: "", isShow: true }
      ],
      tableData: [{}],
      dailyQualityTable:[],
      fiveTableData:[],
      recoveryRateTable:[],
      projectSet: false,
      dialogData: [{
        monitoringItems: "高锰酸钾",
        waterQuality: "",
        spanValue: "",
        zeroPoint: "",
        span: ""
      },{
        monitoringItems: "氨氮",
        waterQuality: "",
        spanValue: "",
        zeroPoint: "",
        span: ""
      },{
        monitoringItems: "总磷",
        waterQuality: "",
        spanValue: "",
        zeroPoint: "",
        span: ""
      },{
        monitoringItems: "总氮",
        waterQuality: "",
        spanValue: "",
        zeroPoint: "",
        span: ""
      }],
      dailyTableHead:[{
        name:"高锰酸盐",
        itemCode:'w01019',
        prop:'acidSalt'
      },{
        name:"氨氮",
        itemCode:'w21003',
        prop:'ammonia'
      },{
        name:"总磷",
        itemCode:'w21011',
        prop:'phosphorus'
      },{
        name:"总氮",
        itemCode:'w21001',
        prop:'nitrogen'
      }],
      tabsValue:'DAY_QUALITY',
      waterSampleTableData:[],
      verificationTableData:[],
      integrateTableData:[],
      fieldList:[]
    };
  },
  computed: {
    stationInfo: {
      get: function() {
        return this.$store.state.stationInfo;
      }
    }
  },
  methods: {
    toggleTab(val) {
      this.getQualityControllerData();
    },
    //查询数据
    getQualityControllerData(){
      this.$http.get("/Quality/qualityAnalysis/qualityControllerExamineData", {
          params: {
            startTime: this.dateRange[0].format('yyyy-MM-dd'),
            endTime: this.dateRange[1].format('yyyy-MM-dd'),
            stationCodes: this.stationId, //this.stationId
            qualityAnalysisType: this.tabsValue,
          }
        }).then(res => {
          if (res.data.code === 0) {
            switch (this.tabsValue) {
              case 'DAY_QUALITY':
                this.dailyQualityTable = res.data.content.dataList;
                break;
              case 'FIVE_PARAM':
                this.fiveTableData = res.data.content.dataList;
                break;
              case 'MULTIPOINT_LINEARITY':
                this.verificationTableData = res.data.content.dataList;
                break;
              case 'INTEGRATED_INTERVENTION':
                this.integrateTableData = res.data.content.dataList;
                break;
              case 'REAL_WATER_SAMPLE_COMPARISON':
                this.waterSampleTableData = res.data.content.dataList;
                break;
              case 'RECOVERY':
                this.recoveryRateTable = res.data.content.dataList;
                break;
            }
          }
        }).catch(error => {});
    },
    //导出表格
    exportExcel(){
      let {tableA,tableB,tableC,tableD,tableE,tableF} = this.$refs;
      let table = null;
      let title = '';
      let headList = [];
      switch (this.tabsValue) {
        case 'DAY_QUALITY':
          table = tableA;
          title = '日质控';
          headList = ['areaName','basinName','stationName','zero_w01019_count','zero_w01019_qualified','zero_w01019_qualifiedRate'
                      ,'zero_w21003_count','zero_w21003_qualified','zero_w21003_qualifiedRate','zero_w21011_count','zero_w21011_qualified','zero_w21011_qualifiedRate'
                      ,'zero_w21001_count','zero_w21001_qualified','zero_w21001_qualifiedRate','span_w01019_count','span_w01019_qualified','span_w01019_qualifiedRate'
                      ,'span_w21003_count','span_w21003_qualified','span_w21003_qualifiedRate','span_w21011_count','span_w21011_qualified','span_w21011_qualifiedRate'
                      ,'span_w21001_count','span_w21001_qualified','span_w21001_qualifiedRate'];
          break;
        case 'FIVE_PARAM':
          table = tableB;
          title = '周质控';
          headList = ['areaName','basinName','stationName','w01010_count','w01010_qualified','w01010_qualifiedRate'
                      ,'w01001_count','w01001_qualified','w01001_qualifiedRate','w01009_count','w01009_qualified','w01009_qualifiedRate'
                      ,'w01014_count','w01014_qualified','w01014_qualifiedRate','w21003_count','w21003_qualified','w21003_qualifiedRate'];
          break;
        case 'MULTIPOINT_LINEARITY':
          table = tableC;
          title = '多点线性核查';
          headList = ['areaName','basinName','stationName','w01019_count','w01019_qualified','w01019_qualifiedRate'
                      ,'w21003_count','w21003_qualified','w21003_qualifiedRate','w21011_count','w21011_qualified','w21011_qualifiedRate'
                      ,'w21001_count','w21001_qualified','w21001_qualifiedRate'];
          break;
        case 'INTEGRATED_INTERVENTION':
          table = tableD;
          title = '集成干预检查';
          headList = ['areaName','basinName','stationName','w01019_count','w01019_qualified','w01019_qualifiedRate'
                      ,'w21003_count','w21003_qualified','w21003_qualifiedRate','w21011_count','w21011_qualified','w21011_qualifiedRate'
                      ,'w21001_count','w21001_qualified','w21001_qualifiedRate'];
          break;
        case 'REAL_WATER_SAMPLE_COMPARISON':
          table = tableE;
          title = '实际水样对比';
          headList = ['areaName','basinName','stationName','w01010_count','w01010_qualified','w01010_qualifiedRate'
                      ,'w01001_count','w01001_qualified','w01001_qualifiedRate','w01009_count','w01009_qualified','w01009_qualifiedRate'
                      ,'w01014_count','w01014_qualified','w01014_qualifiedRate','w21003_count','w21003_qualified','w21003_qualifiedRate']
          break;
        case 'RECOVERY':
          table = tableF;
          title = '加标回收率';
          headList = ['areaName','basinName','stationName','w01019_count','w01019_qualified','w01019_qualifiedRate'
                      ,'w21003_count','w21003_qualified','w21003_qualifiedRate','w21011_count','w21011_qualified','w21011_qualifiedRate'
                      ,'w21001_count','w21001_qualified','w21001_qualifiedRate'];
          break;
      }
      let tablewrap = table.$el;
      // let tableBox = tablewrap.querySelector('.el-table__fixed');//el-table__fixed
      let header = tablewrap.querySelector('.el-table__header');
      
      let params = {
        startTime: this.dateRange[0].format('yyyy-MM-dd'),
        endTime: this.dateRange[1].format('yyyy-MM-dd'),
        stationCodes: this.stationId, //this.stationId
        qualityAnalysisType: this.tabsValue,
        headerProp:headList.join(','),
        tableType:'QUALITY_CONTROLLER_EXAMINE',
        pattern:'yyyy-MM-dd'
      };
      exportTableData(header,title,'','1',"/Quality/exports/qualityControlExcel",params);
    },
  },
  created() {
    this.$store.commit("setShowlefttree", true);
    let startTime = new Date().setDate(new Date().getDate() - 90);
    let endTime = new Date();
    this.dateRange = [ new Date(startTime), endTime];
  },
  watch: {
    stationInfo(value) {
      if (value.length > 0) {
        let list = [];
        value.forEach((item)=>{
          list.push(item.nodeId);
        });
        this.stationId = list.join(',');
        this.getQualityControllerData();
      }
    }
  }
};
</script>

<style scoped lang="less">
.examAnalysis {
  height: calc(100% - 30px);
  .containerbox {
    padding: 6px 12px;
    height: calc(100% - 6px);

  }
}
.editInput {
  width: 50px;
  border: none;
  outline: none;
}
.toolbox-row {
  margin-bottom: 6px;
  &:last-child {
    margin-bottom: 0;
  }
}
</style>



